import React from 'react' 
import { GoHome} from "react-icons/go";
import { StaticQuery, graphql } from 'gatsby'
//import './style.scss'
import Emoji from './emoji'
import { Link } from "gatsby"
const Footer = () => (
  <StaticQuery
    query={graphql`
            query SocialQuery {
              site {
                siteMetadata { 
                  siteUrl
                  author
                }
              }
            }
          `}
    render={data => (
      
      <footer className='footer center has-background-light'>
      
        <div className='content has-text-centered'> 
          <p className='uh'>
            2020 Made with <Emoji emoji=""/>  by <a href="https://dhayzon.com/">{data.site.siteMetadata.author}</a>   {' '}
          </p> 
          <span className='icon'>
            <Link className="pagination-link" to={`/`} rel="next">
                   <GoHome size='36px' color="#512b58" />
                </Link>
             
            </span>
            &nbsp;  
        </div>
      </footer>
    )}
  />
)

export default Footer

 